import {
  LOGIN_END,
  LOGIN_ERROR,
  LOGIN_START,
  LOGOUT_END,
  LOGOUT_ERROR,
  LOGOUT_START,
  REGISTER_END,
  REGISTER_ERROR,
  REGISTER_START,
} from "../action/user-action";

const initialState = {
  user: null,
  isLoading: false,
};

const userReducer = (state = initialState, action = undefined) => {
  switch (action.type) {
    case REGISTER_START:
    case LOGIN_START:
    case LOGOUT_START:
      return {
        ...state,
        isLoading: true,
      };
    case LOGIN_END:
      return {
        ...state,
        user: action.payload,
        isLoading: false,
      };
    case REGISTER_END:
    case LOGOUT_END:
      return {
        ...state,
        isLoading: false,
      };
    case REGISTER_ERROR:
    case LOGIN_ERROR:
    case LOGOUT_ERROR:
      return {
        ...state,
        isLoading: false,
        error: action.error,
      };
    default:
      return state;
  }
};

export default userReducer;
