import classNames from "classnames";
import { Button } from "primereact/button";
import { InputText } from "primereact/inputtext";
import { Panel } from "primereact/panel";
import { Password } from "primereact/password";
import { useCallback, useState } from "react";
import { useDispatch } from "react-redux";
import { fireToastMessage } from "../../services/toast-call-manager";
import { register } from "../../services/user-service";
import constants from "../../store/constants";
import history from "../../store/history";
import { validateUser } from "../../validator/user-validator";

export const RegisterPage = () => {
  const dispatch = useDispatch();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [validation, setValidation] = useState(null);

  const usernameOnChange = useCallback((event) => {
    setUsername(event.target.value);
  }, []);

  const passwordOnChange = useCallback((event) => {
    setPassword(event.target.value);
  }, []);

  const handleRegister = (e) => {
    const user = { username, password };
    const validationResult = validateUser(user);
    if (!validationResult.valid) {
      setValidation(validationResult.fields);
    } else {
      dispatch(register(user))
        .then((result) => {
          if (result) {
            fireToastMessage({
              severity: "success",
              summary: "Success",
              detail: "Successfully Registered",
              life: 3000,
            });
            history.push("/");
          }
        })
        .catch((error) => {
          if (
            error?.data?.status === 400 &&
            (error?.data?.errorType ===
              constants.Exceptions.ProjectArgumentValidationException ||
              error?.data?.errorType ===
                constants.Exceptions.MethodArgumentNotValidException)
          ) {
            setValidation(error.data.fields);
            fireToastMessage({
              severity: "warn",
              summary: "Warning",
              detail: "Correct the invalid fields",
              life: 3000,
            });
          }
        });
    }
    e.preventDefault();
  };

  const passwordToolHeader = <h6>Pick a password</h6>;
  const passwordToolFooter = (
    <>
      <p className="mt-2">Suggestions</p>
      <ul className="pl-2 ml-2 mt-0" style={{ lineHeight: "1.5" }}>
        <li>At least one lowercase</li>
        <li>At least one uppercase</li>
        <li>At least one numeric</li>
        <li>Minimum 8 characters</li>
      </ul>
    </>
  );

  const panelHeader = <h4 className="auth-panel-header">Register</h4>;

  return (
    <div className="vertical-center card clearfix">
      <Panel header={panelHeader}>
        <div className="grid p-fluid">
          <div className="col-12 field">
            <label className="input-label">Username</label>
            <InputText
              style={{ height: "50px" }}
              className={classNames("input-text", {
                "bordered-invalid-field": !!validation && !!validation.username,
              })}
              name="username"
              value={username}
              maxLength={255}
              onChange={usernameOnChange}
            />
            <small className="field-validation-message">
              {validation && validation.username}
            </small>
          </div>

          <div className="col-12 field">
            <label className="input-label">Password</label>
            <Password
              className={classNames("input-text", {
                "bordered-invalid-field": !!validation && validation.password,
              })}
              name="password"
              value={password}
              onChange={passwordOnChange}
              header={passwordToolHeader}
              footer={passwordToolFooter}
              toggleMask={true}
            />
            <small className="field-validation-message">
              {validation && validation.password}
            </small>
          </div>

          <div className="col-12 field">
            <div className="grid">
              <div className="col-12 col-lg-6">
                <a href="/">Login</a>
              </div>
              <div className="col-12 col-lg-6">
                <Button
                  style={{ width: "100%" }}
                  label="Register"
                  icon="pi pi-user-plus"
                  onClick={handleRegister}
                />
              </div>
            </div>
          </div>
        </div>
      </Panel>
    </div>
  );
};
